body{
  background-color: #d4cbe9;
}
.App{
  background-color: #503296;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: rgb(98, 213, 248);
  border: 0; 
  color: rgb(103, 77, 173);
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: rgba(7, 156, 243, 0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgb(155, 200, 243); 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}